.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.Separator__in {
  background: white;
  height: 0.5px;
}
.circles li{
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.2);
    animation: animate 10s linear infinite;
    bottom: -20%;
    
}

.circles li:nth-child(1){
    left: 0%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
}


.circles li:nth-child(2){
    left: 5%;
    width: 40px;
    height: 40px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles li:nth-child(3){
    left: 10%;
    width: 50px;
    height: 50px;
    animation-delay: 4s;
}

.circles li:nth-child(4){
    left: 15%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles li:nth-child(5){
    left: 70%;
    width: 70px;
    height: 70px;
    animation-delay: 3s;
}

.circles li:nth-child(6){
    left: 25%;
    width: 90px;
    height: 90px;
    animation-delay: 3s;
}

.circles li:nth-child(7){
    left: 30%;
    width: 80px;
    height: 80px;
    animation-delay: 7s;
}

.circles li:nth-child(8){
    left: 35%;
    width: 65px;
    height: 65px;
    animation-delay: 15s;
    animation-duration: 45s;
}

.circles li:nth-child(9){
    left: 40%;
    width: 30px;
    height: 30px;
    animation-delay: 2s;
    animation-duration: 35s;
}

.circles li:nth-child(10){
    left: 45%;
    width: 50px;
    height: 50px;
    animation-delay: 17s;
    animation-duration: 11s;
}
.circles li:nth-child(11){
    left: 50%;
    width: 45px;
    height: 45px;
    animation-delay: 14s;
    animation-duration: 11s;
}
.circles li:nth-child(12){
    left: 55%;
    width: 60px;
    height: 60px;
    animation-delay: 7s;
    animation-duration: 11s;
}
.circles li:nth-child(13){
    left: 60%;
    width: 40px;
    height: 40px;
    animation-delay: 3s;
    animation-duration: 11s;
}
.circles li:nth-child(14){
    left: 65%;
    width: 35px;
    height: 35px;
    animation-delay: 1s;
    animation-duration: 14s;
}
.circles li:nth-child(15){
    left: 70%;
    width: 45px;
    height: 45px;
    animation-delay: 5s;
    animation-duration: 15s;
}




@keyframes animate {

    0%{
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }

    100%{
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }

}



